* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}
.body_class{
position: relative;

padding-top: 40px;
padding-bottom: 40px;
}
#search-box {
  background-color: rgb(24, 168, 224);
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.disabled {
  pointer-events: none;
  /* <----------- */
  opacity: 0.5;
}

.input_class{
  position: fixed;
    bottom: (0 + 100vmin);
      
   
}
.btn_class
{
  position: absolute;
  bottom: 0px;
  }

.search-form{
  display: flex;
  justify-content: center;
 
  align-items: center;
  margin-left: 0px;
  margin-right: 0px;
 
  }
    .search-form2 {
  
      margin-left: 0px;
      margin-right: 0px;
      padding-top: 10px;
      margin-bottom: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: baseline;
  row-gap: 15px;
}
.photo-card{
  width: calc((100% - 40px) / 4);
  height: auto;
}

.info {
  
  display: flex;
  justify-content: space-around;

  
}
.info-item{
  display: flex;
  flex-direction: column;
  
  align-items: center;
}

